<template>
    <header class="m-header">
        <!-- 标题头部 -->
        <div class="header border-bottom p-l-30 p-r-30 animated fadeInDown" v-if="headType == 0" v-cloak>
            <div class="m-nav-bar m-pull-left">
                <div class="head-left">
                    <img src='../assets/img/common/font-logo.png' alt />
                </div>
            </div>
            <div class="m-nav-title font-36">
                咨询整形先上美呗网
            </div>
            <div class="m-nav-bar m-pull-right">
                <div class="head-right">
                    <router-link :to="{name:'search'}">
                    <img src="../assets/img/common/icon-search.png" alt />
                    </router-link>
                </div>
            </div>
        </div>

        <!-- 搜索头部 -->
        <div class="header border-bottom animated" v-if="headType == 1" v-cloak>
            <form method="get" action="" autocomplete="off" class="search-box" @submit.prevent="search">
                <div class="m-nav-bar p-l-30">
                    <div slot="left" class="head-left">
                        <span class="icon-search"></span>
                        <input type="search"
                            name="search"
                            v-model="keyword"
                            placeholder="搜索“埋线双眼”试试"
                            class="search font-30"
                        />
                    </div>
                </div>
                <div class="m-nav-bar">
                    <div slot="right" class="head-right" @click="back">取消</div>
                </div>
            </form>
        </div>

        <!-- 项目头部 -->
        <div class="header border-bottom p-l-30 p-r-30 projectheader animated" v-if="headType == 2" v-cloak>
            <div class="m-nav-bar m-pull-left">
                <div class="head-left">
                    <router-link to="/">
                        <img src="../assets/img/common/font-logo.png" alt />
                    </router-link>
                </div>
            </div>
            <div class="m-nav-bar">
                <div class="head-right">
                    <router-link class="search flex flex-between" :to="{name:'search'}">
                        <span class="search-tip font-28 one-line">搜医院、医生、美容项目、部位</span>
                        <img class="search-icon" src="../assets/img/common/icon_search.png" alt />
                    </router-link>
                </div>
            </div>
        </div>

        <!-- 公共头部 -->
        <div class="header border-bottom p-l-30 p-r-30 flex flex-between animated" v-if="headType == 3" v-cloak>
            <div class="m-nav-bar">
                <router-link class="header-logo" to="/">
                    <img v-lazy="'https://cdn-ssl.meb.com/meb_mobile/loadfiles/m/v3.9.6/head_pic_logo.png'" alt="">
                </router-link>
            </div>
            <div class="m-nav-title header-search flex" @click="openSearch">
                <img src="https://cdn-ssl.meb.com/meb_mobile/loadfiles/m/v3.9.6/icon_search.png" alt="">
                <span>搜索医院、医生、美容项目、部位</span>
            </div>
            <div class="m-nav-bar">
                <div class="header-zx" @click="openChat"><img v-lazy="'https://cdn-ssl.meb.com/meb_mobile/loadfiles/m/v3.9.6/head_btn_message.png'" alt=""></div>
            </div>
        </div>
    </header>
</template>
<script>
/**
 * m-header
 * @module components/mContainer
 * @desc 顶部导航
 *
 * @example
    <m-header>
        <div slot="left">返回</div>
        <h1 slot="title" class="m-header-title">首页</h1>
        <div slot="right">分享</div> 
    </m-header>
 */

export default {
  props: {
    headType: {
      type: [Number,String],
      default: 0
    }
  },
  data(){
      return{
          keyword:'',
      }
  },
  methods:{
      //搜索头部
      search(){
          this.$emit('search',this.keyword);
      },
      back(){
          this.$emit('back');
      },
      //公共头部
      openSearch(){
          this.$emit('opensearch',this.keyword)
      },
      openChat(){
          this.$toast.center('功能开发中');
      }
  }
};
</script>

<style lang="less" scoped>
@headerColor: #333;
@headerBg:#FFF;
/*#region public*/
.header{
    width:100%;
    height:100%;
    background:@headerBg;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    white-space: nowrap;
    color: @headerColor;
    justify-content: space-between;
    position:relative;
}
.border-bottom{
    position:relative;
    &::after{
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        border-bottom: 1px solid #e4e4e4;
        width: 200%;
        height: 200%;
        content: "";
        transform: scale(.5);
        transform-origin: left top;
    }
}
.m-nav-bar {
    position:relative;
}
/*#endregion*/
/*#region headType = 0 */

.m-nav-title {
    width:4.4rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight: normal;
    color: @headerColor;
    letter-spacing: 0.0187rem;
}
.m-pull-left,.m-pull-right{
    width:1.25rem;
    overflow:hidden;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.m-pull-left {
    justify-content: flex-start;
    text-align: left;
    font-size: .3rem;
    .head-left {
        height: 0.46rem;
        width: 1rem;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
}

.m-pull-right {
    justify-content: flex-end;
    text-align: right;
    font-size: .3rem;
    .head-right {
        height: 0.46rem;
        width: 0.46rem;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
}
/*#endregion*/

/*#region headType = 1 */
.search-box{
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    .head-left {
        position: relative;
        width: 5.8rem;
        height: 0.6rem;
        border-radius: 5rem;
        background-color: #f1f1f1;
        overflow: hidden;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        .icon-search {
            position: absolute;
            left: 0.15rem;
            display: block;
            z-index: 100;
            height: 0.48rem;
            width: 0.48rem;
            background: url(https://cdn-ssl.meb.com/meb_mobile/loadfiles/search/rewrite/icon_search.png)
            no-repeat center;
            background-size: contain;
        }
        .search {
            background: transparent;
            height: 0.4rem;
            line-height: 0.4rem;
            padding: 0 0.1rem 0 0.8rem;
            margin: 0;
            border: 0;
            color: #222222;
            position: relative;
        }
    }
    .head-right {
        flex: 1;
        width: 1.4rem;
        font-size: 0.3rem;
        text-align: center;
        height: 0.6rem;
        line-height: 0.6rem;
        padding: 0 0.3rem;
        color: #3f3f3f;
    }
}
/*#endregion*/
/*#region headType = 2 */
.projectheader{
   .head-left {
        height: 0.46rem;
        width: 1rem;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    .head-right {
        .search {
            height: 0.6rem;
            width: 5.6rem;
            background: #f1f1f1;
            border-radius: 1rem;
            padding: 0 0.2rem;
            .search-tip {
                color: #666;
            }
            img {
                width: 0.46rem;
                height: 0.46rem;
                object-fit: contain;
            }
        }
    } 
}
/*#endregion*/
/*#region headType = 3*/
.header-logo{
    display: block;
    width: 1.4rem;
    height: 0.5rem;
    img{
        display:block;
        width:100%;
        height:100%;
        object-fit: contain;
    }
}
.header-search{
    width:4.6rem;
    height:.6rem;
    background:#F1F1F1;
    border-radius: 1rem;
    margin-left:.2rem;
    margin-right:.2rem;
    padding:0 .1rem;
    img{
        width:.4rem;
        height:.4rem;
        display:block;
        margin-right:.05rem;
    }
    span{
        color: #ABABAB;
        font-size: .24rem;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }
}
.header-zx{
    width:.5rem;
    height:.5rem;
    img{
        display:block;
        width:100%;
        height:100%;
        object-fit: contain; 
    }
}

/*#endregion*/
</style>


