'use strict';
import http from '../http'
import options from '../http/config'

const hospital = {
    /**
     * 获取医院数据
     * @param {*}
     */
    getData(filter) {
        return http({
            baseURL: options.CMS_API,
            url: "/app/articleapi/news/GetMLatestNews",
            method: "get"
        });
    },

    /**
     * 获取详情数据
     * @param {id}
     */
    getQuestionDetail(filter) {
        return http({
            baseURL: options.CMS_API,
            url: "/app/articleapi/question/GetMQuestionDetail",
            method: "get",
            params: filter
        });
    },

    /**
     * 获取Hospital列表页筛选数据
     * @params {sname=spellname,page=页码}
     */
    getHospitalFilter(filter) {
        return http({
            baseURL: options.YG_API,
            url: "/api/app/v1.0/Project/Select",
            method: "get",
            params: filter
        });
    },
    /**
     * 获取Hospital列表页地区数据
     * @params {}
     */
    getAreaList() {
        return gttp.get("/app/v5/area/getlist");
    },
    /**
     * 获取Hospital列表页数据
     * @params {sname=spellname,page=页码}
     */
    getHospitalList(filter) {
        return http.get("/app/v6/shospital/GetMHospitalList", {
            params: filter
        });
    }
};

export default hospital;


