import http from '../http';
import axios from 'axios';
const qiniu = {
    getToken(){
        return http.get(`/outside/v1/com/FileStore/token?t=${Date.now() + Math.random(99)}`);
    },
    upload(model){
        return axios.post(`https://up.qbox.me`,model,{headers:{'Content-Types':'multipart/form-data'}});
    }
}
export default qiniu;


