// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import api from './plus/api'
import store from './store'
import VueLazyload from 'vue-lazyload'
import BScroll from "better-scroll"

import './components/Toast/toast.css'
import Toast from './components/Toast/index'

Vue.use(Toast);

import vueSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'

import './components'

Vue.config.productionTip = false
Vue.prototype.$http = api
Vue.use(vueSwiper)
Vue.use(VueLazyload, {
    error: "http://cdn.meb.com/default-s",
    loading: ""
});

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App),
    data: {
      eventBus: new Vue()
    }
})

//判断有没有断开连接
window.addEventListener('online', function () {
  store.dispatch('network', {
      onLine: true
  })
});
window.addEventListener('offline', function () {
  store.dispatch('network', {
      onLine: false
  })
});
//判断页面正在加载
window.addEventListener("load", function(event) {
  //console.log("文档以及它包含的资源都已加载完成");
  store.dispatch('loadchange', {
    onLoad: false
  })
});
//判断dom元素加载完成
document.addEventListener("DOMContentLoaded", function(event) {
  //console.log("DOM文档已加载完成");
});
window.onbeforeunload = onbeforeunload_handler;
window.onunload = onunload_handler;
function onbeforeunload_handler() {
  //console.log('在即将离开当前页面(刷新或关闭)时执行');
  store.dispatch('loadchange', {
    onLoad: true
  })
}

function onunload_handler() {
  //console.log('用户退出页面时发生');
  store.dispatch('loadchange', {
    onLoad: false
  })
}

