'use strict';
import http from '../http'
import options from '../http/config'

const home = {
    /**
     * 获取首页数据
     * @param {*}
     */
    getData(filter) {
        return http.get(`/app/v6/shome/GetMHomeIndexV2`, { params: filter });
    },

    /**
     * 获取首页广告数据
     * @param {*}
     */
    getAdData() {
        return http.get('/js/v8/activity/GetActivityAd')
    },

    /**
     * 获取首页微整商品数据
     * @param {*}
     */
    getWzData(filter) {
        return http.get('/app/v8/home/HomeProductList',{params: filter});
    },

    /**
     * 获取首页cms数据
     * @param {*}
     */
    getCmsData(){
        return http({
            baseURL: options.CMS_API,
            method:'get',
            url: '/app/articleapi/Comprehensive/GetMHomeIndexDataV2'
        });
        //return http.get("/app/v6/shome/GetMHomeIndexV2");
    }

};

export default home;


