<template>
    <footer class="m-footer">
        <div class="footer animated slideInUp" v-if="footerType == 0" v-cloak>
            <div class="zx-footer">
                <img src="../assets/img/common/global_jgzx.png" alt="价格咨询">
                <img src="../assets/img/common/global_yyyy.png" alt="预约医院">
            </div>
        </div>
    </footer>
</template>

<script>
export default {
  props: {
    footerType: {
      type: [Number,String],
      default: 0
    }
  }
};
</script>

<style lang="less" scoped>
.m-footer {
    background: transparent;
    font-size: 0.3rem;
    letter-spacing: 0.012rem;
    .footer {
        width:100%;
        height:100%;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        text-align: center;
        white-space: nowrap;
        justify-content: space-between;
        align-items: flex-end;
        position:relative;
    }
}
.zx-footer {
    width:100%;
    height:.88rem;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    white-space: nowrap;
    justify-content: space-between;
    position:relative;
    img {
        width:3.74rem;
        height:100%;
        object-fit: cover
    }
}
</style>



