'use strict';

//import Index from '../views/Index/index';

const Index = q => require.ensure([], () => q(require('../views/Home/index')), 'bundle');
const Test = q => require.ensure([], () => q(require('../views/Home/test')), 'bundle');

const Search = q => require.ensure([], () => q(require('../views/Search/index')), 'bundle');

const Error = q => require.ensure([], () => q(require('../views/Error/index')), 'bundle');

const Example = q => require.ensure([], () => q(require('../views/Example/index')), 'bundle');
const Picker = q => require.ensure([], () => q(require('../views/Example/picker')), 'bundle');

/*
* Project 项目
*/
const Project = q => require.ensure([], () => q(require("../views/Project/index")), "bundle");
const ProjectDetail = q => require.ensure([], () => q(require("../views/Project/detail")), "bundle");
const ProjectZDetail = q => require.ensure([], () => q(require('../views/Project/zdetail')), 'bundle');

/*
* News 整形攻略
*/
const News = q => require.ensure([], () => q(require("../views/News/index")), "bundle");
const NewsList = q => require.ensure([], () => q(require("../views/News/list")), "bundle");
const NewsDetail = q => require.ensure([], () => q(require("../views/News/detail")), "bundle");

/**
 * Zs 整形知识
 */
const Zs = q => require.ensure([], () => q(require("../views/Zs/index")), "bundle");
const ZsDetail = q => require.ensure([], () => q(require("../views/Zs/detail")), "bundle");

/**
 * Question 整形问答
 */
const Question = q => require.ensure([], () => q(require("../views/Question/index")), "bundle");
const QuestionList = q => require.ensure([], () => q(require("../views/Question/list")), "bundle");
const QuestionDetail = q => require.ensure([], () => q(require("../views/Question/detail")), "bundle");

/**
 * Diary 整形日记
 */
const Diary = q => require.ensure([], () => q(require("../views/Diary/index")), "bundle");
const DiaryList = q => require.ensure([], () => q(require("../views/Diary/list")), "bundle");

/*
* Hospital 医院
*/
const HospitalList = q => require.ensure([], () => q(require("../views/Hospital/list")), "bundle");




const routes = [
    {
        path: "*",
        component: Error
    },
    {
        name: "home",
        path: "/home",
        component: Index,
        meta: {
            keepAlive: true,
            title: "首页"
        }
    },
    {
        name: "test",
        path: "/test",
        component: Test,
        meta: {
            keepAlive: false,
            title: "测试"
        }
    },
    {
        name: "example",
        path: "/example",
        component: Example,
        meta: {
            keepAlive: false,
            title: "测试"
        }
    },
    {
        name: "picker",
        path: "/picker",
        component: Picker,
        meta: {
            keepAlive: false,
            title: "picker"
        }
    },
    { path: "/", redirect: "/home" },
    {
        name: "search",
        path: "/search",
        component: Search,
        meta: {
            keepAlive: false,
            title: "搜索"
        }
    },
    //项目百科
    {
        name: "project.index",
        path: "/project",
        component: Project,
        meta: {
            keepAlive: true,
            title: "项目"
        }
    },
    {
        name: "project.detail",
        path: `/project/:id(\\d+)`,
        component: ProjectDetail,
        meta: {
            keepAlive: false,
            title: "项目百科详情"
        }
    },
    {
        name: "project.zdetail",
        path: `/project/:id([z][0-9]+)`,
        component: ProjectZDetail,
        meta: {
            keepAlive: false,
            title: "项目详情"
        }
    },
    //整形攻略
    {
        name: "news.index",
        path: "/news",
        component: News,
        meta: {
            keepAlive: true,
            title: "整形攻略"
        }
    },
    {
        name: "news.list",
        path: "/news/:spellname([a-z]+)/:page(\\d+)?/:parm(\\d+)?",
        component: NewsList,
        meta: {
            keepAlive: false,
            title: "整形攻略分类"
        }
    },
    {
        name: "news.detail",
        path: "/news/:id(\\d+).html",
        component: NewsDetail,
        meta: {
            keepAlive: false,
            title: "整形攻略详情"
        }
    },
    //整形知识
    {
        name: "zs.index",
        path: "/zs",
        component: Zs,
        meta: {
            keepAlive: true,
            title: "整形知识"
        }
    },
    {
        name: "zs.detail",
        path: "/zs/:id(\\d+).html",
        component: ZsDetail,
        meta: {
            keepAlive: false,
            title: "整形知识详情"
        }
    },
    //整形问答
    {
        name: "question.index",
        path: "/question",
        component: Question,
        meta: {
            keepAlive: true,
            title: "整形问答"
        }
    },
    {
        name: "question.list",
        path: "/question/:spellname([a-z]+)/:page(\\d+)?/:parm(\\d+)?",
        component: QuestionList,
        meta: {
            keepAlive: false,
            title: "整形问答分类"
        }
    },
    {
        name: "question.detail",
        path: "/question/:id(\\d+).html",
        component: QuestionDetail,
        meta: {
            keepAlive: false,
            title: "整形问答详情"
        }
    },
    //整形日记 Diary
    {
        name: "diary.index",
        path: "/diary",
        component: Diary,
        meta: {
            keepAlive: true,
            title: "整形日记"
        }
    },
    {
        name: "diary.list",
        path: "/diary/:spellname([a-z]+)/:page(\\d+)?/:parm(\\d+)?",
        component: DiaryList,
        meta: {
            keepAlive: false,
            title: "整形日记"
        }
    },
    //医院
    {
        name: "hospital.list",
        path:
            "/hospital/:spellname([a-z]+)/:areaname?/:page(\\d+)?/:sort(\\d+)?/:type?",
        component: HospitalList,
        meta: {
            keepAlive: false,
            title: "整形医院"
        }
    }
];
export default routes;


