'use strict';
import axios from 'axios';
import options from './config';
import utils from '../utils';
import auth from '../auth';



let instance = axios.create(
    Object.assign(
        {
            baseURL: options.baseURL,
            timeout: 10000
        },
        options
    )
);

let deviceid = utils.uuid.uuid();

// 添加请求拦截器
instance.interceptors.request.use(function (request) {
    // 在发送请求之前做些什么
    let defaults = {
        "content-type": "application/json",
        DeviceVersion: "v3.9.3",
        MebDeviceId: deviceid,
        MebVersion: "v3.9.3",
        UserToken:
            "f69u9q1rFqCTVJIOm%2fVZ3ZtWKCDuskop6Pi1rqH94Q1vqqUg77S6S1BYyIgEB1dBHk04PpXrO%2ftbILEyAoXzf650%2fYUXcQuXFMsBVZTzST2oIo3xBJdAyUfLc3bhW9yN"
    };

    //defaults.UserToken = auth.token;
    request.headers = Object.assign(request.headers, defaults);

    return request;
    
  }, function (error) {
    
    return Promise.reject(error);

  });

// 添加响应拦截器
instance.interceptors.response.use(function (response) {
    return response;
  }, function (error) {
    //请求超时处理
    if (error.message.includes("timeout")) {
        return Promise.reject(error); // reject这个错误信息
    }
    if (error.response) {
        switch (error.response.status) {
            case 401:
                // router.replace({ name: 'signIn' })
                break;
            case 404:
                console.error("接口不存在");
                break;
            case 403:
                console.error("没有访问权限");
                break;
            case 500:
                console.error("服务器错误：" + error.response.data.msg);
                break;
            default:
                console.error("出错了");
                break;
        }
    }

    return Promise.reject(error.response && error.response.data);

  });

export default instance;


