'use strict';

const options = {};

if (process.env.NODE_ENV == 'development') {
    options.baseURL = 'http://api.meb.im';
    options.CMS_API = "http://api.cms.meb.com";
    options.YG_API = 'http://ygapi.meb.im';

    options.host = 'http://m.test.meb.com';

} else {
    options.baseURL = 'https://api.meb.com';
    options.CMS_API = 'http://api.cms.meb.com';
    options.YG_API = 'https://ygapi.meb.com';

    options.host = 'https://m.meb.com';
}

//module.exports = options;

export default options;


