'use strict';
import http from '../http'
import options from '../http/config'

const diary = {
    /**
     * 获取整形咨询首页数据
     * @param {*}
     */
    getData(filter) {
        return http.get( "/app/v6/sdiary/GetMDiaryIndex", {parmas:filter });
    },

    /**
     * 获取详情数据
     * @param {id}
     */
    getNewsDetail(filter) {
        return http({
            baseURL: options.CMS_API,
            url: "/app/articleapi/news/GetMNewsDetail",
            method: "get",
            params: filter
        });
    },

    /**
     * 获取news列表页筛选数据
     * @params {sname=spellname,page=页码}
     */
    getDiaryFilter(filter) {
        return http({
            baseURL: options.YG_API,
            url: "/api/app/v1.0/Project/Select",
            method: "get",
            params: filter
        });
    },
    /**
     * 获取news列表页数据
     * @params {sname=spellname,page=页码}
     */
    getDiaryList(filter) {
        return http.get("/app/v6/sdiary/getmdiarylist", { params: filter });
    }
};

export default diary;


