'use strict';
import http from '../http'
import options from '../http/config'

const search = {
    /**
     * 获取首页数据
     * @param {*}
     */
    getHotData() {
        return http.get(`js/v7/search/HotKeywords`);
    },

    getAdData(){
        return http.get('/js/v8/activity/GetActivityAd');
    },

    getSearch(filter){
        return http({
            baseURL: options.YG_API,
            url:'/api/app/v1.0/Search/SearchAllSimple',
            params:filter,
            methods:'Get'
        })
    }

};

export default search;


