<template>
  <section class="m-center">
    <div class="cover" v-if="pageStatus == 0" v-cloak>
      <div class="loading">
        <div class="sk-double-bounce">
          <div class="sk-child sk-double-bounce1"></div>
          <div class="sk-child sk-double-bounce2"></div>
        </div>
        <!-- <img src="../../static/img/common/loading.gif" /> -->
      </div>
    </div>
    <div class="cover" v-if="pageStatus == 1" v-cloak>
      <div class="noData">
        <slot name="empty">
          <img src="../assets/img/common/no-data.png" />
        </slot>
      </div>
    </div>
    <div class="cover" v-if="!onLine" v-cloak>
      <div class="noData">
        <slot name="noData">
          <img src="../assets/img/common/no-net.png" />
        </slot>
      </div>
    </div>
    <slot></slot>
  </section>
</template>
<script>
import { mapState } from "vuex"
export default {
  props: {
    pageStatus: {
      type: [Number,String],
      default: 0
    }
  },
  data(){
    return{
      swiperOption: {
        direction: "vertical",
        slidesPerView: "auto",
        freeMode: true,
        scrollbar: {
          el: ".swiper-scrollbar"
        },
        mousewheel: true
      },
    }
  },
  computed: {
    ...mapState(['onLine'])
  }
};
</script>
<style lang="less" scoped>
@import url("../assets/less/colors.less");
/*#region 禁止出现滚动条*/
.m-center::-webkit-scrollbar {
  display: none;
}
/*#endregion*/
.cover {
  width: 100%;
  max-width: 414px;
  height: 100%;
  position: fixed;
  top: 0;
  z-index: 100;
  background: #f5f5f5;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #999;
  font-size: 14px;
}

.loading {
  width: .8rem;
  height: .8rem;
  margin: 0 auto;
  position: absolute;
  .sk-double-bounce {
    width: .8rem;
    height: .8rem;
    position: relative;
    margin: 0 auto;
    .sk-child {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: @meb;
      opacity: 0.6;
      position: absolute;
      top: 0;
      left: 0;
      -webkit-animation: sk-doubleBounce 2s infinite ease-in-out;
      animation: sk-doubleBounce 2s infinite ease-in-out;
    }
    .sk-double-bounce2 {
      -webkit-animation-delay: -1s;
      animation-delay: -1s;
    }
  }
}
@-webkit-keyframes sk-doubleBounce {
  0%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes sk-doubleBounce {
  0%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
.loading img {
  width: 100%;
}

.noData,
.noNet > div {
  width: 2.6rem;
  margin: 0 auto;
  position: absolute;
}

.loadingImage img,
.noData img,
.noNet img {
  width: 100%;
}

.noNet {
  display: none;
  width: 100%;
  height: calc(100% - 1.1rem);
  position: absolute;
  top: 1.1rem;
  left: 0;
  background-color: #f5f5f5;
}

.active > .noNet {
  display: block;
  z-index: 99999999999999;
}
</style>


