'use strict';
import http from '../http';

const schoolOpens = {
    /**
     * 获取开学季数据
     * @param {*}
     */
    getSchoolOpensList(filters) {
        return http.get(`/js/v8/Activity/GetEffectTemplateActivityList`,{ params:filters });
    },
};

export default schoolOpens;


