'use strict';
import http from '../http'
import options from '../http/config'

const project = {
    /**
     * 获取项目数据
     * @param {*}
     */
    getProjectData() {
        return http.get(`/app/v5/project/GetProjectList`);
    },

    /**
     * 获取项目信息
     */

    getProjectMsg(filter) {
        return http.get("/app/v6/sproject/GetMZProjectDetail", {
            params: filter
        });
    },

    /**
     * 获取项目相关数据信息
     */

    getProjectRelative(filter) {
        return http({
            baseURL: options.CMS_API,
            url: "/app/articleapi/bff_project/GetRelatedInfomations",
            method: "get",
            params: filter
        });
    },

    /**
     * 获取项目相关日记案例
     * @param {*} filter
     */
    getDiaryData(filter) {
        return http.get("/js/v7/diary/GetDiariesToWapByProjectId", {
            params: filter
        });
    },

    /**
     * 获取项目详情信息
     */
    getProjectDetail(filter) {
        return http.get("/app/v6/sproject/GetMProjectDetail", {
            params: filter
        });
    }
};

export default project;


