export default{
    addAction(context){
        setTimeout(()=>{
            context.commit('add');
            console.log(context.state.count)
        },1000)
        
    },
    reduceAction({commit}){
        commit('reduce')
    },

    network ({ commit, state }, model) {
        commit('network_update', model);
    },

    loadchange ({ commit, state }, model) {
        commit('onload_update', model);
    }

}


